$prefix: '.rise-blocks';

.wp-block-rise-blocks-icon-list {
    padding: 0px;
    display: flex;
    flex-wrap: wrap;
    align-items: center;

    &.rise-blocks-center {
        justify-content: center;
    }

    &.rise-blocks-left {
        justify-content: flex-start;
    }

    &.rise-blocks-right {
        justify-content: flex-end;
    }

    .rise-blocks-content {
        .rise-blocks-title {
            margin: 0 0 0 10px;
        }
    }
}

.aligncenter {
    display: flex;
    justify-content: center;
}
.wp-block-rise-blocks-icon-lists {
    width: 100%;
}

.wp-block-rise-blocks-icon-lists {
    .rise-blocks-parent-block-selector {
        margin-bottom: 20px;
    }
}